import React, { Component } from 'react';
import Gravatar from 'react-gravatar';
import { MenuActive } from '../components/MenuActive';
import { userActions } from '../actions';


class MonsterAside extends Component {

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: '1',
        };

    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab,
            });
        }
    }

    render() {
        // eslint-disable-next-line
        const { user, match, children, ...attributes } = this.props;

        return (
            <React.Fragment>
                <aside className="left-sidebar">
                    
                    <div className="scroll-sidebar">
                        
                        <div className="user-profile">
                            
                            <div className="profile-img"> 
                                <Gravatar email={this.props.data.profile.email} size={50} rating="pg" default="monsterid" />
                            </div>
                            
                            <div className="profile-text"> 
                                <a className="link">
                                    {this.props.data.profile.name}
                                </a>
                            </div>
                        </div>
                        
                        <nav className="sidebar-nav">
                            <ul id="sidebarnav">
                                <li className="nav-small-cap">PERSONAL</li>
                                <MenuActive to={`${match.url}/dashboard`} label="Dashboard" icon="fa fa-dashboard" />
                                <MenuActive to={`${match.url}/technology`} label="Technology" icon="fa fa-twitch" />
                                <MenuActive to={`${match.url}/profile`} label="Profile" icon="fa fa-user" />
                                
                                {/* <li>
                                    <a className="has-arrow " href="#" aria-expanded="false"><i className="mdi mdi-map-marker"></i><span className="hide-menu">With Dropdown</span></a>
                                    <ul aria-expanded="false" className="collapse">
                                        <li><a href="map-google.html">Google Maps</a></li>
                                        <li><a href="map-vector.html">Vector Maps</a></li>
                                    </ul>
                                </li> */}
                            </ul>
                        </nav>
                        
                    </div>
                    
                    {/* <div className="sidebar-footer">
                        <a href="" className="link" data-toggle="tooltip" title="Settings"><i className="ti-settings"></i></a>
                        <a href="" className="link" data-toggle="tooltip" title="Email"><i className="mdi mdi-gmail"></i></a>
                        <a href="" className="link" data-toggle="tooltip" title="Logout"><i className="mdi mdi-power"></i></a>
                    </div> */}
                   
                </aside>
            </React.Fragment>
        );
    }

}

export default MonsterAside;


