import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';

import { history } from '../../helpers';
import { userActions } from '../../actions';
import { alertActions } from '../../actions';

// import Background from '../../assets/images/background/login-register.jpg';

import { 
    Button, Card, CardBody, 
    FormGroup 
} from 'reactstrap';

import { css } from 'react-emotion';
// First way to import
import { BeatLoader } from 'react-spinners';

const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
`;

class LoginPage extends Component {
    constructor(props) {
        super(props);

        console.log(props);

        this.state = {
            loading: true
        }

        // reset login status
        // this.props.dispatch(userActions.logout());
        if(localStorage.getItem('user')){
            history.push('/app/dashboard');
        }

        this.state = {
            email: '',
            password: '',
            submitted: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

        // alert
        const { dispatch } = this.props;
        history.listen((location, action) => {
            // clear alert on location change
            dispatch(alertActions.clear());
        });
    }

    handleChange(e) {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    handleSubmit(e) {
        e.preventDefault();

        this.setState({ submitted: true });
        const { email, password } = this.state;
        const { dispatch } = this.props;
        if (email && password) {
            dispatch(userActions.login(email, password));
        }
    }

    render() {
        const { loggingIn } = this.props;
        const { email, password, submitted } = this.state;
        const { alert } = this.props;

        return (
            <section id="wrapper">
            {/* {{ backgroundImage : `url(${Background})` }} */}
                <div className="login-register" style={{ backgroundColor:"#1C212D" }}>   
                    <Card className="login-box">
                        <CardBody>
                        <form className="form-horizontal form-material" id="loginform" onSubmit={this.handleSubmit}>
                            <h3 className="box-title m-b-20">Masuk Ke Portal</h3>
                            
                            {alert.message &&
                                <div className={`alert ${alert.type}`}>{alert.message}</div>
                            }

                            <FormGroup className={(submitted && !email ? ' has-warning' : '')}>
                                <div className="col-xform-control-feedbas-12">
                                    <input className={'form-control '+(submitted && !email ? ' form-control-warning' : '')} name="email" type="email" value={email} onChange={this.handleChange} required="" placeholder="Email" />
                                </div>
                                {submitted && !email &&
                                    <small className="form-control-feedback">Email is required</small>
                                }
                            </FormGroup>

                            <FormGroup className={(submitted && !password ? ' has-warning' : '')}>
                                <div className="col-xs-12">
                                    <input type="password" className={'form-control '+(submitted && !password ? ' form-control-warning' : '')} name="password" value={password} onChange={this.handleChange} placeholder="Password" />
                                </div>
                                {submitted && !password &&
                                    <small className="form-control-feedback">Password is required</small>
                                }
                            </FormGroup>

                            <div className="form-group text-center m-t-20">
                                <div className="col-xs-12">
                                    <Button color="info" className="btn-block text-uppercase waves-effect waves-light">
                                        Masuk 
                                    </Button>
                                </div>
                            </div>
                            {loggingIn &&
                            <div className='sweet-loading text-center'>
                                <BeatLoader
                                className={override}
                                sizeUnit={"px"}
                                size={10}
                                color={'rgb(54, 215, 183)'}
                                loading={this.state.loading}
                                />
                            </div> 
                            }
                        </form>
                        </CardBody>
                    </Card>
                </div>
            </section>
        );
    }
}

function mapStateToProps(state) {
    const { loggingIn } = state.authentication;
    const { alert } = state;
    return {
        alert,
        loggingIn
    };
}

export default withRouter(connect(mapStateToProps)(LoginPage))


