import React, { Component } from 'react';

class ProfilePage extends Component {
    
    render(){
        const { match } = this.props;
        
        return(
            <div>
                <h1>Welcome to the Profile Page : {match.params.pId}</h1>
            </div>
        );    
    }

}

export default ProfilePage


