import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import MonsterHeader from './MonsterHeader';
import MonsterAside from './MonsterAside';
import routes from '../routes';
import { history } from '../helpers';
import { alertActions } from '../actions/alert.actions';
import { WithSubRoute } from '../components/WithSubRoute';

class MonsterApp extends Component {
    
    constructor(props) {    
        super(props);
    
        const { dispatch } = this.props;
        history.listen((location, action) => {
            // clear alert on location change
            dispatch(alertActions.clear());
        });
    }

    render(){

        const { user, match, dispatch } = this.props;
                
        return(
            <div id="main-wrapper">
                <MonsterHeader data = {user} dispatch={dispatch} />
                <MonsterAside data = {user} match={match} />
            
                <div className="page-wrapper">
                {routes.map((route, idx) => {
                    return route.component ? (
                        <WithSubRoute key={idx} {...route} />
                    ) : console.log(null);
                },)}
                </div>

                <footer className="footer">
                    © 2017 Monster Admin by wrappixel.com
                </footer>
            </div>
        );    
    }

}

function mapStateToProps(state) {
    const { users, authentication } = state;
    const { user } = authentication;
    return {
        user,
        users
    };
}

export default withRouter(connect(mapStateToProps)(MonsterApp))


