import { serviceConstants } from '../constants';
import { authHeader } from '../helpers';
// import axios from 'axios';

export const userService = {
    login,
    logout,
    getMe,
    getAll,
    getById,
    update,
    delete: _delete
};

function login(username, password) {

    let data = JSON.stringify({
        "email" 	: username,
        "password"	: password
    });

    const requestOptions = {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: data
    };

    return fetch(`${serviceConstants.API_URL}/auth/email`, requestOptions)
    .then(handleResponse)
    .then(user => {
        console.log(user);
        // login successful if there's a jwt token in the response
        if (user.token) {
            // store user details and jwt token in local storage to keep user logged in between page refreshes
            localStorage.setItem('user', JSON.stringify(user));
        }

        return user;
    });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
}

function getMe(){
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/me`, requestOptions).then(handleResponse);
}

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/admin/user`, requestOptions).then(handleResponse);
}

function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/users/${id}`, requestOptions).then(handleResponse);
}

function update(user) {
    // const requestOptions = {
    //     method: 'PUT',
    //     headers: { ...authHeader(), 'Content-Type': 'application/json' },
    //     body: JSON.stringify(user)
    // };

    // return fetch(`${serviceConstants.API_URL}/users/${user.id}`, requestOptions).then(handleResponse);;
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/users/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    // console.log(response);
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                logout();
                // location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}


