import React from 'react';
import { css } from 'react-emotion';
import { BeatLoader } from 'react-spinners';

const override = css`
    display: block;
    margin: 0 auto;
    border-color: red;
`;

export const LoadingData = ({ text }) => (
    <BeatLoader
        className={override}
        sizeUnit={"px"}
        size={10}
        color={'rgb(54, 215, 183)'}
        loading={true}
    />
);


