import { technologyConstants } from '../constants';
import { technologyService } from '../services';
import { alertActions } from './';
import { history } from '../helpers';

export const technologyActions = {
    create,
    update,
    getAll,
    getOne,
    delete: _delete
};

function getAll() {
    return dispatch => {
        dispatch(request());

        technologyService.getAll()
            .then(
                technologies => dispatch(success(technologies.data)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: technologyConstants.GETALL_REQUEST } }
    function success(technologies) { return { type: technologyConstants.GETALL_SUCCESS, technologies } }
    function failure(error) { return { type: technologyConstants.GETALL_FAILURE, error } }
}

function getOne(id) {
    return dispatch => {
        dispatch(request());

        technologyService.getById(id)
            .then(
                technology => dispatch(success(technology.data)),
                error => dispatch(failure(error.toString()))
            );
    };

    function request() { return { type: technologyConstants.GETONE_REQUEST } }
    function success(technology) { return { type: technologyConstants.GETONE_SUCCESS, technology } }
    function failure(error) { return { type: technologyConstants.GETONE_FAILURE, error } }
}

function create(tech) {
    return dispatch => {
        dispatch(request(tech));

        technologyService.create(tech)
            .then(
                tech => { 
                    dispatch(success());
                    history.push('/app/technology');
                    dispatch(alertActions.success('Data created.'));
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(tech) { return { type: technologyConstants.CREATE_REQUEST, tech } }
    function success(tech) { return { type: technologyConstants.CREATE_SUCCESS, tech } }
    function failure(error) { return { type: technologyConstants.CREATE_FAILURE, error } }
}

function update(tech, id) {
    return dispatch => {
        dispatch(request(tech));

        technologyService.update(tech, id)
            .then(
                tech => { 
                    dispatch(success());
                    history.push('/app/technology');
                    dispatch(alertActions.success('Data updated.'));
                },
                error => {
                    dispatch(failure(error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(tech) { return { type: technologyConstants.EDIT_REQUEST, tech } }
    function success(tech) { return { type: technologyConstants.EDIT_SUCCESS, tech } }
    function failure(error) { return { type: technologyConstants.EDIT_FAILURE, error } }
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    
    return dispatch => {
        dispatch(request(id));

        technologyService.delete(id)
            .then(
                technology => {
                    dispatch(success(id));
                    dispatch(alertActions.success('Data deleted.'));
                },
                error => {
                    dispatch(failure(id, error.toString()));
                    dispatch(alertActions.error(error.toString()));
                }
            );
    };

    function request(id) { return { type: technologyConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: technologyConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: technologyConstants.DELETE_FAILURE, id, error } }
}


