import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import { technologyActions } from '../../actions';

import { css } from 'react-emotion';
import { LoadingData } from '../../components/LoadingData';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

class TechnologyListPage extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
            tech: null
        };

        this.modDelete = this.modDelete.bind(this);
        this.delete = this.delete.bind(this);
    }

    delete(technology){
        const { dispatch } = this.props;

        console.log(technology);
        dispatch(technologyActions.delete(technology.id));
        dispatch(technologyActions.getAll());
        this.setState({
            modal: !this.state.modal,
            tech: null
        });
    }

    modDelete(technology) {
        this.setState({
            modal: !this.state.modal,
            tech: technology
        });
    }

    componentDidMount() {
        this.props.dispatch(technologyActions.getAll());
    }

    render(){

        const { match, technologies, alert, deleting } = this.props;

        return (
            <div className="container-fluid">
                <div className="row page-titles">
                    <div className="col-md-6 col-8 align-self-center">
                        <h3 className="text-themecolor m-b-0 m-t-0">Technology</h3>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to="/app/dashboard">
                                    Home
                                </Link>
                            </li>
                            <li className="breadcrumb-item active">Technology</li>
                        </ol>
                    </div>
                    <div className="col-md-6 col-4 align-self-center">
                        <Link to={`${match.url}/add`} className="btn pull-right hidden-sm-down btn-success btn-sm">
                            <i className="mdi mdi-plus-circle"></i> Create
                        </Link>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        {alert.message &&
                        <div className={`alert ${alert.type}`}>
                            <button type="button" className="close" data-dismiss="alert" aria-label="Close"> <span aria-hidden="true">&times;</span> </button>
                            {/* <h3 class="text-success"><i class="fa fa-check-circle"></i> Success</h3>  */}
                            {alert.message}
                        </div>
                        }
                        <div className="card">
                            <div className="card-body">
                                <h4 className="card-title">Data Technology</h4>
                                {/* <h6 class="card-subtitle">Create responsive tables by wrapping any <code>.table</code> in <code>.table-responsive </code></h6> */}
                                <div className="table-responsive">
                                                        
                                    <table className="table">
                                        <thead>
                                            <tr>
                                                <th width="10%">Icon</th>
                                                <th>Name</th>
                                                <th>Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        {technologies.loading && 
                                            <tr>
                                                <td colSpan="3">
                                                    <LoadingData text="Loading Technology..."/>
                                                </td>
                                            </tr>
                                        }
                                        {technologies.items &&
                                            technologies.items.map((tech, index) =>
                                                <tr key={tech.id}>
                                                    <td>
                                                        <img src={tech.cover_url} height="50px" />
                                                    </td>
                                                    <td>
                                                        {tech.name}
                                                    </td>
                                                    <td className="text-nowrap">
                                                        <Link to={`${match.url}/${tech.id}/edit`} data-toggle="tooltip" data-original-title="Edit"> 
                                                            <i className="fa fa-pencil text-info m-r-10"></i>
                                                        </Link>
                                                        <a onClick={() => this.modDelete(tech)} data-toggle="tooltip" data-original-title="Delete"> 
                                                            <i className="fa fa-trash text-danger"></i>
                                                        </a>
                                                    </td>
                                                </tr>
                                            )
                                        }
                                        {technologies.error && 
                                            <tr>
                                                <td colSpan="3">
                                                    <div className="label label-table label-danger">
                                                        ERROR: {technologies.error}
                                                    </div>
                                                </td>
                                            </tr>
                                        }
                                        </tbody>
                                    </table>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <Modal isOpen={this.state.modal} toggle={this.modDelete} className={this.props.className}>
                    <ModalHeader toggle={this.modDelete}>
                        Delete {(this.state.tech) ? this.state.tech.name : ''}?
                    </ModalHeader>
                    <ModalBody>
                        Are you sure to delete this data?
                        {deleting &&
                            <LoadingData text="Deleting data..." />
                        }
                    </ModalBody>
                    <ModalFooter>
                        <Button color="danger" className="btn-sm" onClick={() => this.delete(this.state.tech)}>Delete</Button>{' '}
                        <Button color="secondary" className="btn-sm" onClick={() => this.modDelete(this.state.tech)}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );

    }
}

function mapStateToProps(state) {
    const { technologies, authentication, alert } = state;
    const { deleting } = state.technologies;

    return {
        technologies,
        authentication,
        alert,
        deleting
    }
}

export default withRouter(connect(mapStateToProps)(TechnologyListPage))


