import { serviceConstants } from '../constants';
import { authHeader } from '../helpers';

export const technologyService = {
    getAll,
    getById,
    update,
    create,
    delete: _delete
};

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/admin/technology`, requestOptions).then(handleResponse);
}

function getById(id){
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/admin/technology/${id}`, requestOptions).then(handleResponse);
}

function update(tech, id){
    var formData  = new FormData();

    for(var name in tech) {
        formData.append(name, tech[name]);
    }

    const requestOptions = {
        method: 'POST',
        headers: authHeader(),
        body: formData
    };

    return fetch(`${serviceConstants.API_URL}/admin/technology/${id}`, requestOptions).then(handleResponse);
}

function create(tech){
    var formData  = new FormData();

    for(var name in tech) {
        formData.append(name, tech[name]);
    }

    const requestOptions = {
        method: 'POST',
        headers: authHeader(),
        body: formData
    };

    return fetch(`${serviceConstants.API_URL}/admin/technology`, requestOptions).then(handleResponse);
}

function _delete(id){
    
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${serviceConstants.API_URL}/admin/technology/${id}`, requestOptions).then(handleResponse);
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
}

function handleResponse(response) {
    console.log(response);
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                logout();
                // location.reload(true);
            }

            // Provided token is expired. response 400

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}


