import React from 'react';
import Loadable from 'react-loadable';

const prefix = '/app';

function Loading() {
    return <div>Loading...</div>;
}

const Dashboard = Loadable({
    loader: () => import('./pages/DashboardPage'),
    loading: Loading,
});

const Profile = Loadable({
    loader: () => import('./pages/ProfilePage'),
    loading: Loading,
});

const Technology = Loadable({
    loader: () => import('./pages/Technology/TechnologyPage'),
    loading: Loading,
});

const TechnologyList = Loadable({
    loader: () => import('./pages/Technology/TechnologyListPage'),
    loading: Loading,
});

const TechnologyForm = Loadable({
    loader: () => import('./pages/Technology/TechnologyFormPage'),
    loading: Loading,
});

////////////////////////////////////////////////////////////
// then our route config
const routes = [
    {
        path: `${prefix}/dashboard`,
        name: "Dashboard",
        component: Dashboard,
        icon : "fa fa-dashboard"
    },
    {
        path: `${prefix}/profile/:pId`,
        name: "Profile",
        component: Profile,
        icon : "fa fa-user"
    },
    {
        path: `${prefix}/technology`,
        name: "Technology",
        component: Technology,
        icon : "fa fa-twitch",
        routes : [
            {
                path: `${prefix}/technology`,
                name: "Manage Technology",
                component: TechnologyList,
                icon : "fa fa-twitch",
                exact: true
            },
            {
                path: `${prefix}/technology/add`,
                name: "Create Technology",
                component: TechnologyForm,
                icon : "fa fa-twitch"
            },
            {
                path: `${prefix}/technology/:techId/edit`,
                name: "Edit Technology",
                component: TechnologyForm,
                icon : "fa fa-twitch"
            }
        ]
    }
];

export default routes;


