import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

class NotFoundPage extends Component {
    
    render(){
        return(
            <section id="wrapper" className="error-page">
                <div className="error-box">
                    <div className="error-body text-center">
                        <h1 style={{ color:"#1C212D" }}>404</h1>
                        <h3 className="text-uppercase" style={{ color:"#1C212D" }}>Page Not Found !</h3>
                        <p className="text-muted m-t-30 m-b-30">YOU SEEM TO BE TRYING TO FIND HIS WAY HOME</p>
                        
                        <Link to="/" className="btn btn-info btn-rounded waves-effect waves-light m-b-40">Back to home</Link>
                        
                    </div>
                    <footer className="footer text-center">2018 © Malam Minggu Ngoding.</footer>
                </div>
            </section>
        );    
    }

}

export default NotFoundPage


