import { technologyConstants } from '../constants';

export function technologies(state = {}, action) {
  switch (action.type) {
    case technologyConstants.GETALL_REQUEST:
      return {
        loading: true
      };
    case technologyConstants.GETALL_SUCCESS:
      return {
        items: action.technologies
      };
    case technologyConstants.GETALL_FAILURE:
      return { 
        error: action.error
      };
    case technologyConstants.GETONE_REQUEST:
      return {
        loading: true
      };
    case technologyConstants.GETONE_SUCCESS:
      return {
        item: action.technology
      };
    case technologyConstants.GETONE_FAILURE:
      return { 
        error: action.error
      };
    case technologyConstants.CREATE_REQUEST:
      return { creating: true };
    case technologyConstants.CREATE_SUCCESS:
      return {};
    case technologyConstants.CREATE_FAILURE:
      return { 
        error: action.error
      };
    case technologyConstants.EDIT_REQUEST:
      return { updating: true };
    case technologyConstants.EDIT_SUCCESS:
      return {};
    case technologyConstants.EDIT_FAILURE:
      return { 
        error: action.error
      };
    case technologyConstants.DELETE_REQUEST:
      return { deleting: true };
    case technologyConstants.DELETE_SUCCESS:
      return {
        item: action.technology
      };
    case technologyConstants.DELETE_FAILURE:
      return { 
        error: action.error
      };
    default:
      return state
  }
}


