import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import { userActions } from '../actions';

class DashboardPage extends Component {
    
    componentDidMount() {
        this.props.dispatch(userActions.getAll());
    }

    render(){

        const { user, users } = this.props;
        console.log(user);

        return(
            <div className="container-fluid">
                <div className="row page-titles">
                    <div className="col-md-6 col-8 align-self-center">
                        <h3 className="text-themecolor m-b-0 m-t-0">Starter Page</h3>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <a href="/app">Home</a>
                            </li>
                            <li className="breadcrumb-item active">Starter Page</li>
                        </ol>
                    </div>
                    <div className="col-md-6 col-4 align-self-center">
                        <button className="btn pull-right hidden-sm-down btn-success"><i className="mdi mdi-plus-circle"></i> Create</button>
                        <div className="dropdown pull-right m-r-10 hidden-sm-down">
                            <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                January 2017
                            </button>
                            <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                <a className="dropdown-item" href="/">February 2017</a>
                                <a className="dropdown-item" href="/">March 2017</a>
                                <a className="dropdown-item" href="/">April 2017</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        <div className="card">
                            <div className="card-body">
                            <h1>Hi {user.profile.name}!</h1>
                                <p>You're logged in..</p>
                                <h3>users:</h3>
                                {users.loading && <em>Loading users...</em>}
                                {users.error && <span classNameName="text-danger">ERROR: {users.error}</span>}
                                {users.items &&
                                    <ul>
                                        {users.items.map((user, index) =>
                                            <li key={user.id}>
                                                <Link to={"/app/profile/"+user.name} >{user.name + ' - ' + user.email}</Link>
                                            </li>
                                        )}
                                    </ul>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );    
    }

}

function mapStateToProps(state) {
    const { users, authentication } = state;
    const { user } = authentication;
    return {
        user,
        users
    };
}

export default withRouter(connect(mapStateToProps)(DashboardPage))


