import React, { Component } from 'react';
import Gravatar from 'react-gravatar';
import { Link } from 'react-router-dom';

// Images
import logoIcon from '../assets/images/logo-icon.png';
import logoLightIcon from '../assets/images/logo-light-icon.png';
import logoText from '../assets/images/logo-text.png';
import logoLightText from '../assets/images/logo-light-text.png';
import { userActions } from '../actions';

class MonsterHeader extends Component {

    constructor(props) {
        super(props);

        this.handleLogout = this.handleLogout.bind(this);
    }
    
    handleLogout(e) {
        e.preventDefault();

        // reset login status
        this.props.dispatch(userActions.logout());
    }

    render(){
        // eslint-disable-next-line
        const { children, ...attributes } = this.props;

        return(
            <React.Fragment>
            <header className="topbar">
            <nav className="navbar top-navbar navbar-expand-md navbar-light">
               
                <div className="navbar-header">
                    <Link className="navbar-brand" to="/app/dashboard">  
                        <b>
                            <img src={logoIcon} alt="homepage" className="dark-logo" />
                            <img src={logoLightIcon} alt="homepage" className="light-logo" />
                        </b>
                        <span>
                        <img src={logoText} alt="homepage" className="dark-logo" />
                        <img src={logoLightText} className="light-logo" alt="homepage" /></span> 
                    </Link>
                </div>
                
                <div className="navbar-collapse">
                    
                    <ul className="navbar-nav mr-auto mt-md-0 ">
                        <li className="nav-item"> <a className="nav-link nav-toggler hidden-md-up text-muted waves-effect waves-dark" href="javascript:void(0)"><i className="ti-menu"></i></a> </li>
                        <li className="nav-item"> <a className="nav-link sidebartoggler hidden-sm-down text-muted waves-effect waves-dark" href="javascript:void(0)"><i className="icon-arrow-left-circle"></i></a> </li>
                    </ul>
              
                    <ul className="navbar-nav my-lg-0">
                        
                        <li className="nav-item dropdown">
                            <a className="nav-link dropdown-toggle text-muted waves-effect waves-dark" href="" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <Gravatar email={this.props.data.profile.email} size={30} rating="pg" default="monsterid" className="profile-pic" />
                            </a>
                            <div className="dropdown-menu dropdown-menu-right animated flipInY">
                                <ul className="dropdown-user">
                                    <li>
                                        <div className="dw-user-box">
                                            <div className="u-img">
                                                <Gravatar email={this.props.data.profile.email} size={80} rating="pg" default="monsterid" className="profile-img" />
                                            </div>
                                            <div className="u-text">
                                                <h4>{this.props.data.profile.name}</h4>
                                                <p className="text-muted">
                                                    {this.props.data.profile.email}
                                                </p> 
                                                <a href="profile.html" className="btn btn-rounded btn-danger btn-sm">View Profile</a>
                                            </div>
                                        </div>
                                    </li>
                                    <li role="separator" className="divider"></li>
                                    <li><a href="#"><i className="ti-user"></i> My Profile</a></li>
                                    <li><a href="#"><i className="ti-wallet"></i> My Balance</a></li>
                                    <li><a href="#"><i className="ti-email"></i> Inbox</a></li>
                                    <li role="separator" className="divider"></li>
                                    <li><a href="#"><i className="ti-settings"></i> Account Setting</a></li>
                                    <li role="separator" className="divider"></li>
                                    <li>
                                        {/* <Link to="/login"><i className="fa fa-power-off"></i> Logout</Link> */}
                                        <a onClick={this.handleLogout}><i className="fa fa-power-off"></i> Logout</a>
                                    </li>
                                </ul>
                            </div>
                        </li>
                        
                    </ul>
                </div>
            </nav>
        </header>
        </React.Fragment>
        ); 
    }

}

export default MonsterHeader


